import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-database',
  templateUrl: './database.component.html',
  styleUrls: ['./database.component.css']
})
export class DatabaseComponent implements OnInit {
  Title: String;
  Author: String;
  Numbervol: Number;
  Totvol: Number;
  Score: Number;

    
  
   constructor(
    private authService: AuthService,
    private ValidateService: ValidateService,
    private router: Router,
    private flashMessage: FlashMessagesService) { }
    
  ngOnInit() {
  }
  ondatasubmit() {
    const Manga = {
      Title: this.Title,
      Author: this.Author,
      Numbervol: this.Numbervol,
      Totvol: this.Totvol,
      score: this.Score,
    }
    
  

    if(!this.ValidateService.validateData(Manga)){
      console.log(`This Validate service` +Manga)
      this.flashMessage.show('Some Fields are missing', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    this.authService.AddingData(Manga).subscribe(data => {
      if(data.success) {
        this.authService.addMangaData(Manga);
        this.flashMessage.show('Submitted', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/dashboard']);
      } else {
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/']);
      }
    });
  }
}




