import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { HttpModule } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { FlashMessagesService } from 'angular2-flash-messages';

@Injectable()
export class AuthService {
  authToken: any;
  user: any;
  Manga: any;

  constructor(private flashMessage: FlashMessagesService,private http: Http) {
    
      // this.isDev = true;  // Change to false before deployment
      }

  registerUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers: headers})
      .map(res => res.json());
  }

  authenticateUser(user) {
    
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  AddingData(Manga) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('Manga/data', Manga, {headers: headers})
      .map(res => res.json());
  }

  getProfile() {
    let headers = new Headers();
    this.loadToken(); 
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    console.log("1")
    return this.http.get('users/profile', {headers: headers})
      .map(res => res.json());
  }

  storeUserData(token, user) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  // Add manga to the Mongodb
  getManga() {
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    console.log("1")
    return this.http.get('Manga/datashow', {headers: headers})
      .map(res => res.json());
  };

  // storeMangaData(Manga) {
  //   this.flashMessage.show(Manga, {cssClass: 'alert-danger', timeout: 3000});
  //   localStorage.setItem('Manga', JSON.stringify(Manga));
  //   this.Manga = Manga;
  // }

  // Put local data as array
  getMangaData() {
    let mangaData = localStorage.getItem('mangaData');
    if (mangaData) {
      return JSON.parse(mangaData);
    } else {
      return [];
    }
  };
  
  // Function to add new manga data to local storage
  addMangaData(newMangaData) {
    let mangaData = this.getMangaData();
    mangaData.push(newMangaData);
    localStorage.setItem('mangaData', JSON.stringify(mangaData));
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn() {
    return tokenNotExpired('id_token');
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
}



